/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
  @import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
html {
  scroll-behavior: smooth;
}
/*body*/
body{
	font-family: 'Poppins', sans-serif;
}
body{
	background-color: #191919;
}
/*navbar*/
.navul{
	margin: auto;
}
.navul li a{
	text-decoration: none;
	color: #03DAC6;
	margin: 0 80px;
	font-size: 20px;
}
.navul li a:hover, .navul li a:focus{
	border-bottom: #000 solid;
}
.mypic{
    border-radius: 100%;
  }
  .navbar-toggler-icon {
    background-color: white;
    border-radius: 4px;
  }

#navbar{
	position: sticky;
	top: 0;
    left: 0;
	z-index: 100;
	padding: 1.2rem 2rem;
}
nav li{
  text-align: center;
  margin: 10px 0;
}
.navbar-toggler-icon{
background-image: url(./images/togllericon.png) !important;
}
.navbar-toggler-icon{
  background-color: transparent !important;
}
.socialmedia{
  display: flex;
  align-items: center;
  gap: 30px;
}
.socialmedia svg{
  color: #03DAC6;
}
.logo img{
  width: 90px;
  padding: 10px;
}
.navul2 a{
  margin: 0 20px;
}/*section1*/
.s1{
  margin-top: 150px;
  height: 100vh;
}
.s1 h1, .s1 h1 span{
  font-size: 48px;
}
.s1 h1{
	color: #03DAC6;
}
.s1 p{
	color: #a1a1a1;
	font-size: 24px;
	font-weight: 500;
	margin-top: 20px;
}
.s1 span, .about h3{
  color: #000;
}
.s1 img{
  width: 100%;
  border-radius: 50%;
  border: solid 2px #03DAC6;
}
.s1d1{
  border-radius: 50%;
  border: solid 2px #000}
.s1b1 {
  align-items: center;
  appearance: none;
  background-image: radial-gradient(100% 100% at 100% 0, #03DAC6 0, #00685f 100%);
  color: black;
  border: 0;
  border-radius: 6px;
  box-shadow: rgba(45, 35, 66, .4) 0 2px 4px,rgba(45, 35, 66, .3) 0 7px 13px -3px,rgba(58, 65, 111, .5) 0 -3px 0 inset;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif !important;
  font-weight: 500;
  cursor: pointer;
  display: inline-flex;
  height: 48px;
  justify-content: center;
  line-height: 1;
  list-style: none;
  overflow: hidden;
  padding-left: 16px;
  padding-right: 16px;
  position: relative;
  text-align: left;
  text-decoration: none;
  transition: box-shadow .15s,transform .15s;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  white-space: nowrap;
  will-change: box-shadow,transform;
  font-size: 18px;
}
.button-29:focus {
  box-shadow: #3c4fe0 0 0 0 1.5px inset, rgba(45, 35, 66, .4) 0 2px 4px, rgba(45, 35, 66, .3) 0 7px 13px -3px, #3c4fe0 0 -3px 0 inset;
}

.button-29:hover {
  box-shadow: rgba(45, 35, 66, .4) 0 4px 8px, rgba(45, 35, 66, .3) 0 7px 13px -3px, #3c4fe0 0 -3px 0 inset;
  transform: translateY(-2px);
}
.s1b1{
  margin-top: 30px;
}
.button-29:active {
  box-shadow: #3c4fe0 0 3px 7px inset;
  transform: translateY(2px);
}
 /*about*/
 .s2{
  margin-top: 150px;
 }
 .s2 img{
  width: 100%;
 }
 .s2 p{
  margin-top: 20px;
  font-size: 18px;
 }
 .about{
  color: #a1a1a1;
 }
 .about h2{
  color: #03DAC6;
 }
 .about h2{
  font-size: 44px;
 }
 .about h3{
  font-size: 34px;
  margin-top: 20px;
 }
 /*section3 skills*/
 .s3{
  margin-top: 150px;
 }
 .reactimage{
  width: 50px;
  height: 50px;
  margin-top: 20px;
 }
 .cards1 img{
  margin: 10px 0;
 }
 .s3 h2{
  font-size: 44px;
  color: #03DAC6;
  text-align: center;
 }
 .s3 p,.s3 h3{
  color: #fff;
 }
 .cards{
  align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    overflow: hidden;
    border-radius: 14px;
    box-shadow: 20px 20px 60px #101010, -20px -20px 60px #000;
    height: 250px;
    margin-bottom: 33px;
    position: relative;
    width: 100%;
    z-index: 554;
 }
 .cards:hover{
  cursor: pointer;
  box-shadow: 5px 5px 15px #03DAC6, -5px -5px 15px #03DAC6;
 }
 .cardsreact{
  cursor: pointer;
  box-shadow: 5px 5px 15px #03DAC6, -5px -5px 15px #03DAC6;
 }
 .cards1{
  align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    overflow: hidden;
    position: absolute;
 }
 .cards1{
  padding: 20px;
 }
 
.bg1, .blob1 {
    position: absolute;
 }
 /*section4 projects*/
 .s4{
  margin-top: 150px;
 }
 .s4 h2{
  font-size: 44px;
  color: #03DAC6;
  text-align: center;
 }
 .s4 p{
  color: #a1a1a1;
  font-size: 24px;
  text-align: center;
  margin-top: 20px;
 }
 .s4 a{
  text-decoration: none;
  color: #000;
  font-size: 34px;
 }
 .s4 a:hover{
  color: #03DAC6;
 }
 section{
  padding: 0 7px;
 }


/* Mobile Devices */
@media screen and (max-width: 767px) {
  /* header */
  .h11,.lead{
    max-width: 70%;
    }
    .navul li{
     font-size: 14px;
    }

    /* sec */
    section{
      margin-top: 100px !important;
    }
    .s1 h1,  .s1 h1 span, .about h2, .about h3, .s3 h2, .s4 h2{
      font-size: 16px;
    }
    .s1 p, .about p, .cards1 p, .cards1 h3, .s4 p, .s4 h3 a{
      font-size: 14px;
    }
}

